.white {
  color: white;
  font-weight: 70; }

@media screen and (max-width: 768px) {
  .div {
    background: black;
    border-bottom: 3px #b92c2c solid; } }

.navBar {
  background: #333;
  text-align: center;
  border-bottom: 3px #b92c2c solid;
  -webkit-font-smoothing: antialiased; }
  @media screen and (max-width: 768px) {
    .navBar {
      border-bottom: none; } }

button.logo {
  border: none;
  cursor: pointer; }
  button.logo:focus {
    outline: 0; }

.aWhite {
  color: white; }
  .aWhite:hover {
    color: #acacac;
    text-decoration: none; }

.mainTitle {
  font-size: 80px;
  width: 100vw;
  text-align: center;
  font-weight: 900;
  text-transform: uppercase;
  cursor: pointer; }

.mainText {
  font-size: 1.3em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center; }

.hide {
  background: #707070;
  color: white; }
  @media screen and (max-width: 1170px) {
    .hide {
      display: none; } }
